.App {
  text-align: center;
  background: #360033; 
  background: -webkit-linear-gradient(to bottom, #0b8793, #360033);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #063358, #0b0123); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  color: #fff;
overflow: hidden;
padding: 5px;
}

button{
  background: none;
  border: 2px solid rgb(240, 138, 5);
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover{
  border: 2px solid rgb(141, 101, 50);


}
.card-grid{
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr  1fr 1fr ;
  grid-gap:20px;
  padding: 50px;

}

.head{
  color:rgb(234, 80, 4);
  font-weight: 800;
  font-size: xx-large;
  padding:20px;
  margin-bottom: 20px;
  margin-top: 30px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

@media (max-width: 500px) {
  .card-grid{
    margin-top: 0px;
    display: grid;
    grid-template-columns: 1fr 1fr ;
    grid-gap:20px;
    
  }
  
  }