.card{
    position: relative;
    box-shadow: rgba(71, 69, 69, 0.35) 0px 5px 15px;
     border-radius: 10px;

}
.card .front{
 width: 300px;
 height: 200px;
 display: block;
 border: 2px solid rgb(1, 0, 0) ;
 border-radius: 10px;
  transform:rotateY(90deg);
    position: absolute;
    transition: all ease-in 0.2s;
}

.flipped .front{
    transform:rotateY(0deg);
    transition-delay: 0.2s;
}
.card .back{
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
     width: 300px;
     height: 200px;
     border-radius: 10px;


}

.flipped .back{
    transform:rotateY(90deg);
    transition-delay: 0s;
}

@media (max-width: 450px) {

    .card .front{
        width: 220px;
        height: 150px;
   
       }
       
    .card .back{
         width:220px;
         height: 150px;
    }
    
}  